import styled from "styled-components"
import Logo from "../../assets/Logo.png"
import { useNavigate } from "react-router-dom"
import { useEffect, useState } from "react"
import { Menu, X } from "lucide-react"
import { colors } from "../../styles/colors"
import { useGetInfo } from "../../apis/auth"
import { tempCookie } from "../../utils/tempCookie"

export function Header() {
    const navigate = useNavigate()
    const [menuOpen, setMenuOpen] = useState(false)

    const token = tempCookie.getAccessToken()
    const { data: userInfo } = useGetInfo({
        enabled: !!token,
        retry: 0,
    })

    const isLogined = !!userInfo

    const toggleMenu = () => setMenuOpen((prev) => !prev)

    const handleNavClick = (path: string) => {
        navigate(path)
        setMenuOpen(false)
    }

    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth > 768) setMenuOpen(false)
        }
        window.addEventListener("resize", handleResize)
        return () => window.removeEventListener("resize", handleResize)
    }, [])

    return (
        <Container>
            <Wrapper>
                <LogoButton onClick={() => navigate("/")}>
                    <img src={Logo} alt="" />
                </LogoButton>

                <NavCenter>
                    {isLogined && (
                        <>
                            <NavButton onClick={() => navigate("/device")}>
                                
                            </NavButton>
                            <NavButton onClick={() => navigate("/area")}>
                                
                            </NavButton>
                        </>
                    )}
                </NavCenter>

                <RightSection>
                    <DesktopButtons>
                        {isLogined ? (
                            <NavButton onClick={() => navigate("/mypage")}>
                                
                            </NavButton>
                        ) : (
                            <>
                                <NavButton
                                    onClick={() => navigate("/register")}
                                >
                                    
                                </NavButton>
                                <LoginButton onClick={() => navigate("/login")}>
                                    
                                </LoginButton>
                            </>
                        )}
                    </DesktopButtons>

                    <MenuButton onClick={toggleMenu}>
                        {menuOpen ? <X size={24} /> : <Menu size={24} />}
                    </MenuButton>
                </RightSection>
            </Wrapper>

            {menuOpen && (
                <MobileMenu>
                    {isLogined ? (
                        <>
                            <NavButton
                                onClick={() => handleNavClick("/device")}
                            >
                                
                            </NavButton>
                            <NavButton onClick={() => handleNavClick("/area")}>
                                
                            </NavButton>
                            <NavButton
                                onClick={() => handleNavClick("/mypage")}
                            >
                                
                            </NavButton>
                        </>
                    ) : (
                        <>
                            <NavButton
                                onClick={() => handleNavClick("/register")}
                            >
                                
                            </NavButton>
                            <NavButton onClick={() => handleNavClick("/login")}>
                                
                            </NavButton>
                        </>
                    )}
                </MobileMenu>
            )}
        </Container>
    )
}

const Container = styled.header`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: white;
    border-bottom: 1px solid #eee;
    z-index: 1000;
`

const Wrapper = styled.div`
    max-width: 1200px;
    margin: 0 auto;
    height: 66px;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
`

const LogoButton = styled.button`
    background: none;
    border: none;
    cursor: pointer;

    img {
        width: 80px;
        height: auto;

        @media (max-width: 500px) {
            width: 60px;
        }
    }
`

const NavCenter = styled.nav`
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 24px;

    @media (max-width: 768px) {
        display: none;
    }
`

const RightSection = styled.div`
    display: flex;
    align-items: center;
    gap: 16px;
`

const DesktopButtons = styled.div`
    display: flex;
    gap: 16px;
    align-items: center;

    @media (max-width: 768px) {
        display: none;
    }
`

const NavButton = styled.button`
    background: none;
    border: none;
    font-size: 16px;
    color: #333;
    cursor: pointer;
    white-space: nowrap;

    &:hover {
        color: ${colors.Green500};
    }

    @media (max-width: 500px) {
        font-size: 14px;
    }
`

const LoginButton = styled.button`
    background: ${colors.Green500};
    color: white;
    border: none;
    padding: 7px 12px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 15px;

    &:hover {
        opacity: 0.9;
    }

    @media (max-width: 500px) {
        font-size: 14px;
        padding: 6px 10px;
    }
`

const MenuButton = styled.button`
    background: none;
    border: none;
    cursor: pointer;
    display: none;

    @media (max-width: 768px) {
        display: block;
    }
`

const MobileMenu = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    background: white;
    border-top: 1px solid #eee;
    padding: 12px 0;
    animation: fadeIn 0.3s ease;

    ${NavButton}, ${LoginButton} {
        margin: 8px 0;
    }

    @keyframes fadeIn {
        from {
            opacity: 0;
            transform: translateY(-5px);
        }
        to {
            opacity: 1;
            transform: translateY(0);
        }
    }
`
