import styled from "styled-components"
import { Container, AreaItem } from "../components/setting"
import { Button, Dropdown, Input, Text } from "../components/common"
import { useForm } from "../hooks/useForm"
import { useState } from "react"

interface Device {
    id: number
    name: string
    state: "success" | "fail"
}

interface Area {
    id: number
    plant: string
    name: string
    devices: Device[]
}

export function Setting() {
    const { form, handleChange } = useForm<{
        device_id: string
        device_password: string
    }>({
        device_id: "",
        device_password: "",
    })

    const [old, setOld] = useState<string>("")
    const [checkedAreas, setCheckedAreas] = useState<number[]>([])
    const [checkedDevices, setCheckedDevices] = useState<number[]>([])

    const handleAdd = (e: React.FormEvent<HTMLButtonElement>) => {
        e.preventDefault()
    }

    const dummyAreas: Area[] = [
        {
            id: 1,
            plant: "",
            name: " A",
            devices: [
                { id: 1, name: " 1", state: "success" },
                { id: 2, name: " 2", state: "fail" },
            ],
        },
        {
            id: 2,
            plant: "",
            name: " B",
            devices: [],
        },
        {
            id: 3,
            plant: "",
            name: " C",
            devices: [],
        },
    ]

    const dummyNotSettingItems = new Array(3)
        .fill("")
        .map((_, i) => ` ${i + 3}`)

    const handleCheckArea = (id: number) => {
        setCheckedAreas((prev) =>
            prev.includes(id) ? prev.filter((a) => a !== id) : [...prev, id]
        )
    }

    const handleCheckDevice = (id: number) => {
        setCheckedDevices((prev) =>
            prev.includes(id) ? prev.filter((d) => d !== id) : [...prev, id]
        )
    }

    const handleAddToOld = () => {
        //    
    }

    const handleAddNew = () => {
        //    
    }

    return (
        <Wrapper>
            <Container title=" ">
                <Input
                    label=" "
                    placeholder="  "
                    value={form.device_id}
                    name="device_id"
                    onChange={handleChange}
                />
                <Input
                    label=" "
                    placeholder="  "
                    value={form.device_password}
                    name="device_password"
                    onChange={handleChange}
                />
                <Button size="large" onClick={handleAdd}>
                    
                </Button>
            </Container>

            <Container title=" " gap={8}>
                {dummyAreas.map((area) => (
                    <div
                        style={{ width: "100%", marginBottom: "0px" }}
                        key={area.id}
                    >
                        <AreaItem
                            checkbox={true}
                            type="areaDelete"
                            plant={area.plant}
                            name={area.name}
                            value={checkedAreas.includes(area.id)}
                            onClick={() => console.log(area.name, "")}
                            onCheck={() => handleCheckArea(area.id)}
                        />

                        {area.devices.length > 0 && (
                            <DeviceList>
                                {area.devices.map((device) => (
                                    <DeviceItemWrapper key={device.id}>
                                        <AreaItem
                                            checkbox={true}
                                            type="deviceDelete"
                                            name={device.name}
                                            value={checkedDevices.includes(
                                                area.id
                                            )}
                                            state={device.state}
                                            onClick={() =>
                                                console.log(device.name, "")
                                            }
                                            onCheck={() =>
                                                handleCheckDevice(device.id)
                                            }
                                        />
                                    </DeviceItemWrapper>
                                ))}
                            </DeviceList>
                        )}
                    </div>
                ))}
            </Container>

            <Container title=" ">
                <ContentContainer>
                    <Text font="TitleTiny"> </Text>
                    {dummyNotSettingItems.map((v, i) => (
                        <AreaItem
                            checkbox={true}
                            name={v}
                            key={i}
                            button={false}
                        />
                    ))}
                </ContentContainer>

                <ContentContainer>
                    <Dropdown
                        label="  "
                        options={dummyAreas.map((v) => v.name)}
                        onChange={setOld}
                        placeholder="  "
                        value={old}
                    />
                    <ButtonWrapper>
                        <Button onClick={handleAddToOld} size={"large"}>
                            
                        </Button>
                    </ButtonWrapper>
                </ContentContainer>

                <ContentContainer>
                    <Input
                        label="  "
                        placeholder="    "
                    />
                    <ButtonWrapper>
                        <Button onClick={handleAddNew} size={"large"}>
                            
                        </Button>
                    </ButtonWrapper>
                </ContentContainer>
            </Container>
        </Wrapper>
    )
}

const Wrapper = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 80px;
    padding: 100px 0;
    box-sizing: border-box;

    @media (max-width: 1024px) {
        gap: 60px;
        padding: 80px 32px;
    }

    @media (max-width: 768px) {
        gap: 40px;
        padding: 80px 20px;
    }
`

const DeviceList = styled.div`
    width: 100%;
    display: flex;
    align-items: end;
    flex-direction: column;
    gap: 8px;
    margin-top: 8px;
`

const DeviceItemWrapper = styled.div`
    width: 95%;

    @media (max-width: 768px) {
        width: 100%;
    }
`

const ContentContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    gap: 5px;
`

const ButtonWrapper = styled.div`
    width: 120px;
    margin-left: auto;

    @media (max-width: 768px) {
        width: 100%;
        margin-left: 0;
    }
`
