import { useMutation, useQuery } from "@tanstack/react-query"
import { AxiosError } from "axios"
import { instance } from ".."
import { tempCookie } from "../../utils/tempCookie"

import {
    EditRequest,
    InfoResponse,
    LoginRequest,
    LoginResponse,
    RegisterRequest,
} from "./type"

export const useRegister = () => {
    const { mutate, ...rest } = useMutation<
        number,
        AxiosError,
        RegisterRequest
    >({
        mutationFn: async (data) => {
            const response = await instance.post("/auth/signup", data)
            return response.status
        },
        onError: (error) => {
            console.error(" :", error.response?.data)
        },
    })

    return { mutate, ...rest }
}

export const useLogin = () => {
    const { mutate, ...rest } = useMutation<number, AxiosError, LoginRequest>({
        mutationFn: async (data) => {
            const response = await instance.post<LoginResponse>(
                "/auth/login",
                data
            )

            if (response.data.accessToken && response.data.refreshToken) {
                tempCookie.setAccessToken(response.data.accessToken)
                tempCookie.setRefreshToken(response.data.refreshToken)
            } else {
                console.error("  ")
            }

            return response.status
        },
        onError: (error) => {
            console.error(" :", error.response?.data)
            tempCookie.clearTokens()
        },
    })

    return { mutate, ...rest }
}

export const useLogout = () => {
    const { mutate, ...rest } = useMutation<number, AxiosError>({
        mutationFn: async () => {
            const accessToken = tempCookie.getAccessToken()
            const refreshToken = tempCookie.getRefreshToken()

            if (!accessToken && !refreshToken) {
                tempCookie.clearTokens()
                return 200
            }

            try {
                await instance.post("/auth/logout", undefined, {
                    withCredentials: true,
                    headers: {
                        Authorization: accessToken
                            ? `Bearer ${accessToken}`
                            : "",
                    },
                })
                tempCookie.clearTokens()
                return 200
            } catch (error: any) {
                tempCookie.clearTokens()
                throw error
            }
        },
        onSuccess: () => {
            console.log(" ")
        },
        onError: (error) => {
            console.error(" :", error.response?.data)
        },
    })

    return { mutate, ...rest }
}

export const useExit = () => {
    const { mutate, ...rest } = useMutation<number, AxiosError>({
        mutationFn: async () => {
            const accessToken = tempCookie.getAccessToken()
            const refreshToken = tempCookie.getRefreshToken()

            if (!accessToken && !refreshToken) {
                tempCookie.clearTokens()
                return 200
            }

            try {
                await instance.delete("/me", {
                    withCredentials: true,
                    headers: {
                        Authorization: accessToken
                            ? `Bearer ${accessToken}`
                            : "",
                    },
                })
                tempCookie.clearTokens()
                return 200
            } catch (error: any) {
                tempCookie.clearTokens()
                throw error
            }
        },
        onError: (error) => {
            console.error("  :", error.response?.data)
        },
    })

    return { mutate, ...rest }
}

export const useEdit = () => {
    const { mutate, ...rest } = useMutation<number, AxiosError, EditRequest>({
        mutationFn: async (data) => {
            const accessToken = tempCookie.getAccessToken()

            if (!accessToken) {
                return Promise.reject(new Error(" ."))
            }

            const response = await instance.patch("/me", data, {
                withCredentials: true,
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                },
            })

            return response.status
        },
        onError: (error) => {
            console.error("   :", error.response?.data)
        },
    })

    return { mutate, ...rest }
}

export const useGetInfo = (options = {}) => {
    return useQuery<InfoResponse, AxiosError>({
        queryKey: ["userInfo"],
        queryFn: async () => {
            const response = await instance.get<InfoResponse>("/me")
            return response.data
        },
        staleTime: 1000 * 60 * 5,
        retry: 1,
        ...options,
    })
}
