import styled from "styled-components"
import { Button, Input, Text } from "../components/common"
import { colors } from "../styles/colors"
import { useForm } from "../hooks/useForm"
import { useNavigate } from "react-router-dom"
import { toast } from "react-toastify"
import { useEdit, useExit, useLogout } from "../apis/auth"

export function Mypage() {
    const navigate = useNavigate()

    const { form, handleChange } = useForm<{
        name: string
        password: string
        newPassword: string
    }>({
        name: "",
        password: "",
        newPassword: "",
    })

    const { mutate: editInfo, isPending } = useEdit()
    const { mutate: logout } = useLogout()
    const { mutate: exit } = useExit()

    const handleUserInfo = (e: React.FormEvent<HTMLButtonElement>) => {
        e.preventDefault()

        if (
            (form.password && !form.newPassword) ||
            (!form.password && form.newPassword)
        ) {
            toast.error("   .")
            return
        }

        if (
            form.password &&
            form.newPassword &&
            form.password === form.newPassword
        ) {
            toast.error("     .")
            return
        }

        const payload: Partial<{ name: string; password: string }> = {}
        if (form.name.trim()) payload.name = form.name.trim()
        if (form.newPassword.trim()) payload.password = form.newPassword.trim()

        if (Object.keys(payload).length === 0) {
            toast.info("  .")
            return
        }

        editInfo(payload, {
            onSuccess: () => toast.success(" ."),
            onError: (error) =>
                toast.error(error.message || "    ."),
        })
    }

    const handleLogout = () => {
        logout(undefined, {
            onSuccess: () => {
                toast.success(" .")
                navigate("/login")
            },
            onError: () => toast.error(" ."),
        })
    }

    const handleDeleteUser = () => {
        if (!window.confirm("  ?")) return
        exit(undefined, {
            onSuccess: () => {
                toast.success(" .")
                navigate("/")
            },
            onError: () => toast.error(" ."),
        })
    }

    return (
        <Container>
            <Wrapper>
                <Form>
                    <Input
                        label=""
                        placeholder=" "
                        name="name"
                        value={form.name}
                        onChange={handleChange}
                    />
                    <Input
                        label=" "
                        type="password"
                        placeholder="  "
                        name="password"
                        value={form.password}
                        onChange={handleChange}
                    />
                    <Input
                        label=" "
                        type="password"
                        placeholder="  "
                        name="newPassword"
                        value={form.newPassword}
                        onChange={handleChange}
                    />
                    <Button
                        size="large"
                        color={isPending ? "gray" : "main"}
                        onClick={handleUserInfo}
                        disabled={isPending}
                    >
                         
                    </Button>
                </Form>

                <Logout>
                    <Button size="large" kind="gray" onClick={handleLogout}>
                        
                    </Button>
                </Logout>

                <Delete>
                    <Text font="LabelLarge">
                               
                    </Text>
                    <DeleteButton>
                        <Button
                            size="large"
                            kind="red"
                            onClick={handleDeleteUser}
                        >
                            
                        </Button>
                    </DeleteButton>
                </Delete>
            </Wrapper>
        </Container>
    )
}

const Container = styled.div`
    width: 100%;
    padding: 100px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;

    @media (max-width: 1024px) {
        padding: 80px 32px;
    }

    @media (max-width: 768px) {
        padding: 80px 20px;
    }
`

const Wrapper = styled.div`
    width: 100%;
    max-width: 760px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 60px;
    padding: 100px 0;
    box-sizing: border-box;

    @media (max-width: 1024px) {
        gap: 48px;
        padding: 80px 32px;
    }

    @media (max-width: 768px) {
        gap: 36px;
        padding: 80px 20px;
    }

    @media (max-width: 480px) {
        padding: 60px 16px;
        gap: 28px;
    }
`

const Form = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 24px;

    @media (max-width: 480px) {
        gap: 16px;
    }
`

const Logout = styled.div`
    margin-left: auto;
    width: 150px;

    @media (max-width: 480px) {
        width: 100%;
    }

    button {
        width: 100%;
    }
`

const Delete = styled.div`
    width: 100%;
    padding: 12px;
    border: 1px solid ${colors.Gray300};
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 12px;
    box-sizing: border-box;

    @media (max-width: 768px) {
        flex-direction: column;
        align-items: flex-start;
    }

    @media (max-width: 480px) {
        font-size: 14px;
        padding: 10px;
        gap: 8px;
    }
`

const DeleteButton = styled.div`
    width: 150px;
    margin-left: auto;

    @media (max-width: 480px) {
        width: 100%;
    }

    button {
        width: 100%;
    }
`
