import styled from "styled-components"
import { Input, Button, Text } from "../components/common"
import { useForm } from "../hooks/useForm"
import { useLogin } from "../apis/auth"
import { useNavigate } from "react-router-dom"
import { toast } from "react-toastify"

export function Login() {
    const navigate = useNavigate()

    const { form, handleChange } = useForm<{
        user_id: string
        password: string
    }>({
        user_id: "",
        password: "",
    })

    const { mutate: login, isPending } = useLogin()

    const handleLogin = (e: React.FormEvent<HTMLButtonElement>) => {
        e.preventDefault()
        if (!form.user_id || !form.password) {
            toast.error("  ")
            return
        }

        login(
            { id: form.user_id, password: form.password },
            {
                onSuccess: () => navigate("/"),
                onError: (error) =>
                    toast.error(
                        error.message || "    ."
                    ),
            }
        )
    }
    return (
        <Container>
            <Wrapper>
                <Form>
                    <TitleBox>
                        <Text font="TitleLarge"></Text>
                        <Text font="BodyMedium" color="Gray500">
                                .
                        </Text>
                    </TitleBox>
                    <InputBox>
                        <Input
                            label=""
                            placeholder="  "
                            name="user_id"
                            value={form.user_id}
                            onChange={handleChange}
                            required
                            autoComplete="username"
                        />
                        <Input
                            label=""
                            placeholder="  "
                            name="password"
                            type="password"
                            value={form.password}
                            onChange={handleChange}
                            required
                            autoComplete="current-password"
                        />
                    </InputBox>
                    <ButtonBox>
                        <Button
                            size="large"
                            full
                            color={isPending ? "gray" : "main"}
                            onClick={handleLogin}
                            disabled={isPending}
                        >
                            
                        </Button>
                        <QuestionBox>
                            <Text font="BodySmall" color="Gray500">
                                  ?
                            </Text>
                            <a href="/register">
                                <Text font="LabelMedium" color="Green500">
                                    
                                </Text>
                            </a>
                        </QuestionBox>
                    </ButtonBox>
                </Form>
            </Wrapper>
        </Container>
    )
}

const Container = styled.div`
    width: 100%;
    padding: 100px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;

    @media (max-width: 1024px) {
        padding: 80px 32px;
    }

    @media (max-width: 768px) {
        padding: 80px 20px;
    }
`

const Wrapper = styled.div`
    width: 100%;
    max-width: 760px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 60px;
    box-sizing: border-box;

    @media (max-width: 1024px) {
        gap: 48px;
        padding: 80px 32px;
    }

    @media (max-width: 768px) {
        gap: 36px;
        padding: 80px 20px;
    }

    @media (max-width: 480px) {
        padding: 60px 16px;
        gap: 28px;
    }
`

const Form = styled.form`
    display: flex;
    flex-direction: column;
    gap: 48px;
    width: 100%;
    max-width: 480px;
    padding: 60px 32px;

    @media (max-width: 768px) {
        padding: 40px 20px;
        gap: 36px;
    }
`

const TitleBox = styled.div`
    display: flex;
    flex-direction: column;
    gap: 8px;
`

const InputBox = styled.div`
    display: flex;
    flex-direction: column;
    gap: 24px;
`

const ButtonBox = styled.div`
    display: flex;
    flex-direction: column;
    gap: 24px;
    align-items: center;
`

const QuestionBox = styled.div`
    display: flex;
    gap: 8px;
    justify-content: center;
    align-items: center;
`
